<template>
  <div class="textCard">
      <span class="desc">{{content.desc}}</span>
      <p class="icon"><img :src="imgSrc"></p>
  </div>
</template>

<script>
export default {
  name: 'TextCard',
  props: {
      content:{
          type:Object,
          default(){
              return{}
          }
      }
  },
  data(){
    return{
        imgSrc:require("@assets/images/down.png")
    }
  }
}
</script>


<style scoped lang='less'>
.textCard{
    background: #fff;
    margin: .1rem 0;
    border-radius: .1rem;
    padding: .12rem .2rem;
    text-align: center;
    line-height: .54rem;
    .icon{
        width: .4rem;
        height: .4rem;
        margin: 0 auto;
        img{
            width: 100%;
        }
    }
}
</style>
